import React, { useState } from "react";
import "./First.css";
import ProgressBar from "react-bootstrap/ProgressBar";
const backBtn = require("../../Assets/botao_voltar_proximo.svg");
const pauseBtn = require("../../Assets/botao_pause.svg");
const twitch_icon = require("../../Assets/twitch_icon.svg");
const youtube_icon = require("../../Assets/youtube_icon.svg");
const facebook_icon = require("../../Assets/facebook_icon.svg");

export default function First({ previousAnswer, changeScreen, answer }) {
  const [active, setActive] = useState(null);

  const handleSubmit = () => {
    answer(1, active === 0 ? true : false);
    return changeScreen(1);
  };

  const handleYoutube = () => {};
  const handleFacebook = () => {};
  const handleTwitch = () => {};

  return (
    <div className="container">
      <div className="game-content">
        <div className="question-container">
          <p className="question-title mb-4">
            {previousAnswer
              ? "Qual plataforma voc√™ utiliza?"
              : "Voc√™ j√° sabe que tipo de conte√∫do vai postar?"}
          </p>
        </div>
        {previousAnswer ? (
          <div className="button-container">
            <button
              className="button"
              type="button"
              onClick={() => handleYoutube()}
            >
              <img src={youtube_icon} className="logo-social-media" />
              <p>Youtube</p>
            </button>
            <button
              className="button"
              type="button"
              onClick={() => handleFacebook()}
            >
              <img src={facebook_icon} className="logo-social-media" />
              <p>Facebook</p>
            </button>
            <button
              className="button"
              type="button"
              onClick={() => handleTwitch()}
            >
              <img src={twitch_icon} className="logo-social-media" />
              <p>Twitch TV</p>
            </button>{" "}
          </div>
        ) : (
          <div className="button-container">
            <button className="button-box" onClick={() => setActive(0)}>
              <p className={active === 0 ? "clicked-button" : "yn-button"}>
                Sim
              </p>
            </button>
            <button className="button-box" onClick={() => setActive(1)}>
              <p className={active === 1 ? "clicked-button" : "yn-button"}>
                N√£o
              </p>
            </button>
          </div>
        )}
        <div className="progress-bar">
          <ProgressBar
            animated
            variant="danger"
            now={10}
            style={{ marginBottom: 20 }}
          />
        </div>
        <div className="player-button-container">
          <img
            className="player-button"
            alt="Imagem de voltar de um player"
            src={backBtn}
          />
          <img
            className="player-button"
            alt="Bot√£o de pause de um player"
            src={pauseBtn}
          />
          <button onClick={() => handleSubmit()}>
            <img
              className="player-button-right"
              alt="Imagem de ir para frente de um player"
              src={backBtn}
            />
          </button>
        </div>
      </div>
    </div>
  );
}
