import React, { useState } from "react";
import Slider from "react-input-slider";
import ProgressBar from "react-bootstrap/ProgressBar";
const backBtn = require("../../Assets/botao_voltar_proximo.svg");
const pauseBtn = require("../../Assets/botao_pause.svg");

export default function Fourth({ previousAnswer, changeScreen, answer }) {
  const [daysInMonth, setDaysInMonth] = useState(0);

  const handleSubmit = () => {
    answer(1, daysInMonth);
    return changeScreen(1);
  };

  return (
    <div className="container">
      <div className="game-content">
        <div className="question-container">
          <p className="question-title">
            {previousAnswer[0] === null
              ? previousAnswer[1].answer
                ? "Qual a m√©dia de dias no m√™s que voc√™ tem disponibilidade para fazer stream?"
                : ""
              : ""}
          </p>
        </div>
        <div className="button-container">
          {previousAnswer[0] === null ? (
            previousAnswer[1].answer ? (
              <>
                <p className="info-title">{daysInMonth} dias</p>
                <Slider
                  styles={{
                    track: {
                      height: 50,
                      borderRadius: 25
                    },
                    active: {
                      backgroundColor: "#DC3545",
                      height: 50,
                      borderRadius: 25
                    },
                    thumb: {
                      width: 55,
                      height: 55,
                      backgroundColor: "#aaa"
                    },
                    disabled: {
                      opacity: 0.5
                    }
                  }}
                  xmax={30}
                  axis="x"
                  x={daysInMonth}
                  onChange={({ x }) => setDaysInMonth(x)}
                />
              </>
            ) : (
              ""
            )
          ) : (
            ""
          )}
        </div>
        <div className="progress-bar">
          <ProgressBar
            animated
            variant="danger"
            now={25}
            style={{ marginBottom: 20 }}
          />
        </div>
        <div className="player-button-container">
          <img
            className="player-button"
            alt="Imagem de voltar de um player"
            src={backBtn}
          />
          <img
            className="player-button"
            alt="Bot√£o de pause de um player"
            src={pauseBtn}
          />
          <button onClick={() => handleSubmit()}>
            <img
              className="player-button-right"
              alt="Imagem de ir para frente de um player"
              src={backBtn}
            />
          </button>
        </div>
      </div>
    </div>
  );
}
